.loginWrapper {
    height: 100vh;
    margin-top: -180px;
    background-color:#f0f0f0;
  }
  .loginContainer {
    position: relative;
    background-color: rgb(255, 255, 255);
    margin-left: auto;
    margin-right: auto;
    top: 275px;
    max-width: 398px;
  }
  .loginContainer__form {
    padding: 0 25px;
  }
  #loginLabel {
    font-size: 28px;
    padding-top: 25px;
    padding-left: 25px;
  }
  #redirect {
    text-align: center;
    margin-top: 25px;
    padding-bottom: 25px;
  }
  .loginContainer__loginButton {
    height: 40px;
    width: 100%;
    text-align: center;
    cursor: pointer;
    margin-top: 10px;
    background-color: #028858;
    color: white;
    font-size: 15px;
    border: none;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.335, 1);
  }
  .loginContainer__loginButton:hover {
  background-color: #015738;
  }
  .loginContainer__loginButton:focus {
  outline: none;
  }
  .form-input {
    margin-top: 20px;
    min-height: 42px;
    width: 100%;
    color: #111;
    background-color: #fff;
    border-color: #ddd;
    padding: 8px 16px;
    font-size: 16px;
    border-style: solid;
    border-width: 1px;
  }