* {
    margin:0;
  }
  
  .signup_master-box {
    background-color: #f0f0f0;
    height: 100vh;
    margin-top: -180px;
  }
  
  .signup-redirect {
    box-sizing: border-box;
    background-color: #FCFCFB;
    color: #3D3D66;
    padding: 20px;
    font-size: 16px;
    height: 62px;
    border-bottom: 1px solid #F1EEEA;
  }
  
  .signup-container {
    box-sizing: border-box;
    background-color: rgb(255, 255, 255);
    position: relative;
    margin-top: 0;
    left: 0;
    right: 0;
    top: 275px;
    bottom: 50%;
    text-align: center;
    min-height: 525px;
    max-width: 398px;
    margin: auto;
  }
  
  .signup-input {
    box-sizing: border-box;
    min-height: 42px;
    margin-bottom: 15px;
    width: 90%;
    color: #282828;
    background-color: #fff;
    border-color: #DCDEDD;
    padding: 8px 16px;
    font-size: 14px;
    border-style: solid;
    border-width: 1px;
  }
  .signup-input:focus {
    box-shadow: 0 0 0 1px #009E74;
    outline: 0;
  }
  .bad-input {
    outline: none;
    border-color: red;
    box-sizing: border-box;
    min-height: 48px;
    width: 90%;
    color: #111;
    background-color: #fff;
    padding: 8px 16px;
    margin: 4px;
    font-size: 16px;
    border-style: solid;
    border-width: 2px;
  }
  .signup-button {
    box-sizing: border-box;
    min-height: 40px;
    height: 40px;
    width: 90%;
    text-align: center;
    cursor: pointer;
    background-color: #028858;
    color: white;
    font-size: 14px;
    border: none;
    transition: all 300ms cubic-bezier(0.175, 0.885, 0.335, 1);
  }
  .signup-button:hover {
    background-color: #015738;
  }
  .signup-button:focus {
    outline: none;
  }
  #blue {
    color: blue
  }
  .signup-label {
    margin-top: 30px;
    margin-bottom: 30px;
    padding-left: 20px;
    text-align: left;
    font-size: 28px;
  }
  